
*{
  margin:0;
  padding: 0;
}
img{
  vertical-align: middle;
  border:none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  /*-webkit-font-smoothing: antialiased;*/
  /*-moz-osx-font-smoothing: grayscale;*/
}
.head-img {
  height: 80px;
  width: 80px;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
  margin: 50px auto 50px;
  position: relative;
}
.content-web {
  width: 964px;
  height: 280px;
  margin: 50px auto 0;
  border: 1px solid #ddd;
  padding:20px 0 0 0;
}
.content-web ul {
  list-style: none;
  position: relative;
  margin: 0;
  padding: 0;
}
.head-img .show-p {
  position: absolute;
 -webkit-animation: show 1s;
         animation: show 1s;
  bottom: 0px;
  text-align: center;
  width: 80px;
  margin: 0;
  padding: 0;
  background-color: #333;
  opacity: 0.2;
  height: 12px;
}
@-webkit-keyframes show {
from{bottom:-12px;
}
to{bottom:0px
}
}
@keyframes show {
from{bottom:-12px;
}
to{bottom:0px
}
}
ul li a{
  text-decoration: none;
  padding:0;
  margin:0;
}
ul li{
  list-style: none;
  margin:0 0 20px 20px;
  float:left;
}
ul li img{
  height:120px;
  width:216px;
  background-color: #333333;
}
h1 {
  margin-top: 30px;
  text-align: center;
}

